								"lyric": "[00:00.00] 作曲 : 李建衡\n[00:00.783] 作词 : 择荇\n[00:02.350]\n[00:02.640]（729声工场）\n[00:03.220]少年智则国智，少年富则国富，少年强则国强，少年独立则国独立！\n[00:15.780]\n[00:37.990]（HITA、小爱的妈）\n[00:38.590]怒海如沸，搏云击月，\n[00:42.630]我举火向雪原，长庚星不肯灭。\n[00:48.930]\n[00:51.000]（清弄、许多葵）\n[00:51.130]鸡唱黄河，霜冻未解，\n[00:54.430]我回身一抱拳，朝故乡道个别。\n[01:00.730]\n[01:01.550]（橙翼、陈鹏杰）\n[01:01.700]听悲笳画角吹过万里风，\n[01:05.200]卷过旌旗重，待我拉开这张弓，\n[01:12.840]\n[01:13.370]（奇然、阿册）\n[01:13.590]问满城铁甲谁取天骄种，\n[01:17.090]谁敢缚苍龙，造就不世出英雄。\n[01:25.210]\n[01:25.340]（萧忆情、排骨教主）\n[01:25.560]时龙战于野，其血玄黄，奔雷摇北斗，\n[01:31.030]便拍马飞渡，击楫中流，恭迎我神州，\n[01:36.890]\n[01:37.030]（萧忆情、排骨教主、HITA、爱妈、清弄、许多葵、橙翼、陈鹏杰、奇然、阿册）\n[01:37.190]教百川参拜，关河俯首，热土长留，\n[01:40.790]莫等到，廉颇老，再为江山寿！\n[01:49.010]\n[01:49.170]（慕斯小乖、五年、孙翔）\n[01:49.280]高歌酹酒，来者相候，\n[01:54.810]唤我今日，长缨在手。\n[02:01.600]\n[02:24.970]（绯村柯北、上古）\n[02:25.750]故国登临，不及领略，\n[02:28.970]我匆匆歃过血，旧风景未忘却，\n[02:36.460]\n[02:37.580]（银临、不才）\n[02:37.780]乾坤万卷，胸中醒觉，\n[02:41.100]我投笔换戎装，向烽火赴个约。\n[02:47.540]\n[02:48.220]（小千、阿杰）\n[02:48.350]听悲笳画角吹过万里风，\n[02:51.720]卷过旌旗重，待我拉开这张弓，\n[02:59.540]\n[03:00.010]（蛙蛙、贰婶）\n[03:00.200]问满城铁甲谁取天骄种，\n[03:03.320]谁敢缚苍龙，造就不世出英雄。\n[03:11.440]\n[03:11.880]（音频怪物、李常超）\n[03:12.030]时龙战于野，其血玄黄，奔雷摇北斗，\n[03:17.580]便拍马飞渡，击楫中流，恭迎我神州，\n[03:23.250]\n[03:23.520]（音频怪物、李常超、绯村柯北、上古、银临、不才、小千、阿杰、蛙蛙、贰婶）\n[03:23.680]教百川参拜，关河俯首，热土长留，\n[03:27.380]莫等到，廉颇老，再为江山寿！\n[03:36.360]\n[03:50.170]（全员）\n[03:50.420]时大争之世，鹿鼎春秋，会猎于诸侯，\n[03:56.220]便九天揽月，五洋擒蛟，庇佑我神州，\n[04:02.080]教尘寰不老，初心不灭，长锋不朽，\n[04:06.110]莫等到，古人云，白我少年头。\n[04:13.870]\n[04:14.050]（猫饭、灰老板）\n[04:14.230]高歌酹酒，来者相候，\n[04:20.000]唤我今日，长缨在手。\n[04:26.540]\n[04:27.540]混音：Mr.鱼\n[04:28.540]念白：729声工场\n"
