								"lyric": "[00:00.850]夢ならばどれほどよかったでしょう\n[00:06.650]未だにあなたのことを夢にみる\n[00:12.340]忘れた物を取りに帰るように\n[00:17.660]古びた思い出の埃を払う\n[00:25.820]戻らない幸せがあることを\n[00:31.580]最後にあなたが教えてくれた\n[00:36.980]言えずに隠してた昏い過去も\n[00:42.550]あなたがいなきゃ永遠に昏いまま\n[00:48.230]きっともうこれ以上 傷つくことなど\n[00:53.750]ありはしないとわかっている\n[00:58.660]あの日の悲しみさえ\n[01:01.410]あの日の苦しみさえ\n[01:04.150]そのすべてを愛してた あなたとともに\n[01:09.800]胸に残り離れない\n[01:12.730]苦いレモンの匂い\n[01:15.730]雨が降り止むまでは帰れない\n[01:21.240]今でもあなたはわたしの光\n[01:37.600]暗闇であなたの背をなぞった\n[01:42.980]その輪郭を鮮明に覚えている\n[01:48.800]受け止めきれないものと出会うたび\n[01:54.400]溢れてやまないのは涙だけ\n[01:59.990]何をしていたの\n[02:02.760]何を見ていたの\n[02:05.580]わたしの知らない横顔で\n[02:10.390]どこかであなたが今\n[02:13.380]わたしと同じ様な\n[02:15.950]涙にくれ 淋しさの中にいるなら\n[02:21.470]わたしのことなどどうか 忘れてください\n[02:27.490]そんなことを心から願うほどに\n[02:32.910]今でもあなたはわたしの光\n[02:41.600]自分が思うより 恋をしていたあなたに\n[02:52.410]あれから思うように 息ができない\n[03:03.360]あんなに側にいたのにまるで嘘みたい\n[03:14.140]とても忘れられないそれだけが確か\n[03:30.430]あの日の悲しみさえ\n[03:33.200]あの日の苦しみさえ\n[03:35.910]その全てを愛してたあなたと共に\n[03:41.320]胸に残り離れない\n[03:44.280]苦いレモンの匂い\n[03:47.730]雨が降り止むまでは帰れない\n[03:52.840]切り分けた果実の片方の様に\n[03:58.590]今でもあなたはわたしの光\n"
