								"lyric": "[00:00.00] 作曲 : 赵钶\n[00:01.00] 作词 : 赵钶\n[00:32.320]编    曲  谭畅\n[00:33.830]城墙根，来碗面，忒色\n[00:41.470]德福巷，喝一壶，舒坦\n[00:48.690]曲江池，转一宿，宝钏牵着平贵的手，寒窑的故事听不够。\n[01:03.990]兵马俑，不开口，谁人懂\n[01:11.270]杨贵妃，红尘泪，别问是与非\n[01:18.090]感慨烽火台前戏诸侯，秦王华夏大一统\n[01:25.500]听闻八仙各自显神通\n[01:28.930]谁陪我丝绸之路一叩首\n[01:34.340]吼一曲秦腔\n[01:38.110]喋一碗泡馍\n[01:42.290]八百里秦川看尽波澜壮阔\n[01:49.400]拜一拜佛陀\n[01:52.960]再谝一谝传说\n[01:57.170]看三千年多少豪杰是非功过\n[02:04.810]回民坊里经过，钟鼓楼前穿梭，谁把前朝的故事说了又说，玄奘骑着白马，秦王安然睡着，看三千年来太平盛世和爱恨纠葛\n[02:58.290]兵马俑，不开口，谁人懂\n[03:05.730]杨贵妃，红尘泪，别问是与非\n[03:12.410]感慨烽火台前戏诸侯，秦王华夏大一统\n[03:19.820]听闻八仙各自显神通\n[03:23.320]谁陪我丝绸之路一叩首\n[03:28.740]吼一曲秦腔\n[03:32.460]喋一碗泡馍\n[03:36.690]八百里秦川看尽波澜壮阔\n[03:43.840]拜一拜佛陀\n[03:47.270]再谝一谝传说\n[03:51.480]看三千年多少豪杰是非功过\n[03:59.140]回民坊里经过，钟鼓楼前穿梭，谁把前朝的故事说了又说，玄奘骑着白马，秦王安然睡着，看三千年来太平盛世和爱恨纠葛\n[04:28.780]吼一曲秦腔\n[04:32.450]喋一碗泡馍\n[04:36.630]八百里秦川看尽波澜壮阔\n[04:43.740]拜一拜佛陀\n[04:47.240]再谝一谝传说\n[04:51.510]看三千年多少豪杰是非功过\n[04:59.180]回民坊里经过，钟鼓楼前穿梭，谁把前朝的故事说了又说，玄奘骑着白马，秦王安然睡着，看三千年来太平盛世和爱恨纠葛\n[05:17.230]\n[05:18.980]制作人  王路遥\n[05:20.220]吉    他  苏宏亮\n[05:21.060]大鼓&鼓     迟功伟(大伟)\n[05:22.140]古    筝  丁雪儿\n[05:22.920]二    胡  丁晨泽\n[05:23.430]弦    乐   灵魂室内乐团\n[05:23.900]大提琴   赵旭阳\n[05:24.310]小提琴   杨莹\n[05:24.640]录音师  丁晨泽@2496 Top Music\n[05:24.950]混    音  王路遥@2496 Top Music\n[05:25.250]录音棚   2496 Top Music Studio\n"
